@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

body{
 margin: 0;
 padding:0;
 font-family: "poppins";
 background-color: rgb(12,236, 206);

}

*{
 box-sizing: border-box;

}

.todo{
 display: flex;
 flex-direction: column;
 margin: 100px auto;
 max-width: 300px;
 border-radius: 7%;
 min-height: 400px;
 padding: 20px;
 background-color: #fff;
 color: rgb (40, 136,123);
 -webkit-border-radius: 7%;
 -moz-border-radius: 7%;
 -ms-border-radius: 7%;
 -o-border-radius: 7%;

}

.todo-item{
 display:flex;
 justify-content: flex-start;

}

.ml-1{
 margin-left:10px;

}


form{
 display:flex;
 width:100%;
 margin:20px auto;
 justify-content: space-between;

}

form input{
 width: 75%;
 padding: 7px;
 border: none;
 border-bottom: 1px solid ;
 outline: none;

}

::placeholder{
 font-size:15px ;
 letter-spacing: 2px;
 color: #888;

}

form button{
 background-color: transparent;
 border:1px solid #999;
 width: 13%;
 cursor:pointer;
 border-radius: 5px;
 padding: 7px;
 -webkit-border-radius: 5px;
 -moz-border-radius: 5px;
 -ms-border-radius: 5px;
  -o-border-radius: 5px;

}

button:hover{
 background-color: #000;
 border-color: #000;
 color: #fff;

}